# Page Templates
--------------------------------------------------------------*/
/* Front page */
.banner-container-title, .about-title, .portfolio-title, .skillset-title, .contact-title {
  font-family: Montserrat, Arial, sans-serif;
  text-transform: uppercase;
  font-size: 60; }

.hero-banner {
  height: calc(100vh - 40px); }

.banner-container {
  text-align: center; }

.banner-title-span {
  color: #FFDD00;
  font-size: 50px; }

.about-title::after {
  content: '';
  display: block;
  width: 80%;
  height: 2px;
  background-color: #5979D2; }
